        <div class="post-content center">
        <div class="workPageNav">
        <router-link to="/work/logos" active-class="active">Logos</router-link> |
        <router-link to="/work/photography" active-class="active">Photography</router-link> |
        <router-link to="/work/websites" active-class="active">Websites</router-link> |
        <router-link to="/work/print" active-class="active">Print</router-link>
        </div>
        </div>
      </article>
    </div>
  </div>
</div>
</template>

<script>
  import SiteHeader from '../components/Header.vue'
  import JaggedHr from '../components/Elements/JaggedHr.vue'
  export default {
    components: {
      JaggedHr,
