<script>
  import SiteHeader from '../components/Header.vue'
  import JaggedHr from '../components/Elements/JaggedHr.vue'

  export default {
    components: {
      JaggedHr,
      SiteHeader
    },
    props: {
      ztitle: ''
    },
    created () {
      document.title = 'Photography | Andrew Finney Weilbaecher'
    }
  }
  
</script>

<template>
<div>
  <SiteHeader ztitle='Photography'></SiteHeader>
  <WorkNav></WorkNav>
  <div class="container single workContainer">
    <div class="posts">
      <article class="post">
        <h1 class="post-title">Photography</h1>
        <div class="post-meta">
        </div>
        <JaggedHr></JaggedHr>
        <div class="post-content center">

          <div class="workItem-wrapper">
          <div class="workItem-left"><img src="../../static/images/photography-img1.jpg" data-action="zoom"></div>
          <div class="workItem-right">
          <div class="workItem-title">Andy Bread</div>
          <div class="workItem-tags">Photography | Mixed Media</div>
          </div>
          </div>

          <div class="workItem-wrapper_var1">
          <div class="workItem-left_var1"><img src="../../static/images/photography-img2.jpg" data-action="zoom"></div>
          <div class="workItem-right_var1">
          <div class="workItem-title">City Park Twirler</div>
          <div class="workItem-tags">Photography</div>
          </div>
          </div>

          <div class="workItem-wrapper">
          <div class="workItem-left"><img src="../../static/images/photography-img3.jpg" data-action="zoom"></div>
          <div class="workItem-right">
          <div class="workItem-title">Boudreaux the Dog</div>
          <div class="workItem-tags">Photography</div>
          </div>
          </div>

          <div class="workItem-wrapper_var1">
          <div class="workItem-left_var1"><img src="../../static/images/photography-img4.jpg" data-action="zoom"></div>
          <div class="workItem-right_var1">
          <div class="workItem-title">Downtown New Orleans</div>
          <div class="workItem-tags">Photography</div>
          </div>
          </div>

        </div>
      </article>
    </div>
  </div>
</div>
</template>




